@tailwind base;
@tailwind components;
@tailwind utilities;


@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.rees{
    padding: 2rem;
    display: flex;
    align-items: center;
    flex-direction: column;
}
.rees button{
    width: 15rem;
}
.mygrid{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 1rem;
    width: 100%;
    padding: 1rem;
    box-sizing: border-box;
    font-size: 1rem;
}
.fix-all{
    padding: 1rem;
}
.nav-bar{
    /* background-color: rgb(22, 23, 54); */
    padding: 1rem;
    padding-left: 2.4rem;
}
.nav-bar ul{
    display: flex;
    gap: 0.4rem;
}
.nav-bar a{
    color: #ffffff;
    font-size: 0.9rem;
    border-radius: 10px; 
    padding: 0.6rem 2rem;
}
.nav-bar a:hover{
    background-color: rgba(183, 186, 245, 0.153);
}
.nav-bar .active{
    background-color: rgb(10, 11, 30) !important;
}

.box-width{
    width: 100%;
    border-radius: 20px;
    font-size: 0.85rem;
}

/* styles.css (or App.css) */

html {
    height: 100%;
  }
  
  body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  #root {
    flex: 1;
  }
  